import { Button, Card } from "@greenpool-eco/components";
import * as React from "react";
import * as ReactDOM from "react-dom";

import "./styles.scss";
import { isBrowser } from "utils/is-browser";

type LayoutProps = {
  backText: string;
  isOpen?: boolean;
  container?: Element;
  onClose: () => void;
};

export const Layout: React.FC<LayoutProps> = ({
  backText,
  children,
  isOpen = true,
  container = isBrowser() ? document.body : null,
  onClose,
}) => {
  if (!isOpen || container == null) {
    return null;
  }

  return ReactDOM.createPortal(
    <div className={"modal modal_overlay"}>
      <Card className={"modal__card "}>
        {children}
        <Button onClick={onClose}>{backText}</Button>
      </Card>

      <div className="modal-background modal_overlay" />
    </div>,
    container
  );
};
